        }

      for (var i = 0; i < variableArray.length; i++) {

        var textRow = ``
        //Make a row of data items
        for (var item in variableArray[i]) {
          //textRow.push(`<td>${item}</td>`)
          textRow += `<td>${variableArray[i][item]}</td>`
        }

        //Insert the data row into the table
        var text = `<tr>` + textRow + `</tr>`

        //console.log(text)
        document.getElementById('data-table').insertAdjacentHTML('beforeend', text)
      }

      console.log(variableArray)
      this.data.variableArray = variableArray
              
    },
