          if (searchBy === 'name') item = sortedVariableArray[j].name
          else if (searchBy === 'description') item = sortedVariableArray[j].description
          else if (searchBy === 'database') item = sortedVariableArray[j].database
          else if (searchBy === 'dataset') item = sortedVariableArray[j].dataset
          else if (searchBy === 'type') item = sortedVariableArray[j].type
          else if (searchBy === 'category') item = sortedVariableArray[j].category
          else if (searchBy === 'source') item = sortedVariableArray[j].source
          else if (searchBy === 'dbt_database') item = sortedVariableArray[j].dbt_database
          else item = sortedVariableArray[j].name

          if (searchBy === 'name') item2 = sortedVariableArray[j + 1].name
          else if (searchBy === 'description') item2 = sortedVariableArray[j + 1].description
          else if (searchBy === 'database') item2 = sortedVariableArray[j + 1].database
          else if (searchBy === 'dataset') item2 = sortedVariableArray[j + 1].dataset
          else if (searchBy === 'type') item2 = sortedVariableArray[j + 1].type
          else if (searchBy === 'category') item2 = sortedVariableArray[j + 1].category
          else if (searchBy === 'source') item2 = sortedVariableArray[j + 1].source
          else if (searchBy === 'dbt_database') item2 = sortedVariableArray[j + 1].dbt_database
          else item2 = sortedVariableArray[j + 1].name

          //Compares the values of two items and swaps them based on the smallest/largest one
          if (option === "increase") {
            if (item2 > item) {
              var temp = sortedVariableArray[j]
              sortedVariableArray[j] = sortedVariableArray[j + 1]
              sortedVariableArray[j + 1] = temp
            }

          }
          if (option === "decrease") {
            if (item2 < item) {
              temp = sortedVariableArray[j]
              sortedVariableArray[j] = sortedVariableArray[j + 1]
              sortedVariableArray[j + 1] = temp
            }

          }

        }

      }

      //Update the variable array
      this.data.variableArray = sortedVariableArray

      //Re-render the table results displayed
      this.displayResults(this.data.searchBy, sortedVariableArray)*/
    },
    //Exports the data as a .CSV file
    exportData() {
      var data = this.data.variableArray
      var textArr = [];

      //Adds the headings based on the first row's object's property names
      var headingsRow = [];
      for (var property in data[0]) {
        headingsRow.push(property);
      }
      textArr.push(headingsRow)

      //Push each data row
      for (var i = 0; i < data.length; i++) {
        var textRow = [];
        for (var item in data[i]) {
          textRow.push(data[i][item])
        }
        textArr.push(textRow)
      }

      //Format the data row
      var text = textArr.join('\n');

      //Create a link that downloads the file
      var link = document.createElement('a');
      link.setAttribute(
        'href',
        'data:text/csv;charset=utf-8,' + encodeURI(text)
      );
      link.setAttribute('download', 'data_csv.csv');


      //Add the link to the HTML document so that it
      link.style.display = 'none';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  }

